*{
  padding: 0;
  margin: 0 auto;
  box-sizing: border-box;
  outline: none;
  font-family: sans-serif;
}
.container{
  margin-top: 80px;
  margin-bottom: 40px;
  width: 280px;
  min-height: 400px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0px 0px 14px -8px;
  padding: 20px;
}
.title{
  width:100%;
  text-align: center;
  padding: 4px 0px 8px 0px;
  border-bottom: 1px solid lightgrey;
}
.title img{
  width: 60px;
}
.main{
  margin-top: 18px;
  width: 100%;
  display: flex;
  padding: 0px 0px 18px 0px;
  border-bottom: 1px solid lightgrey;
}
.main input{
  width: 78%;
  padding: 8px;
  flex: 1;
  border: 1px solid lightgrey;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
.main button{
  width: 20%;
  background: #000;
  color: #fff;
  border: none;
  cursor: pointer;
  padding: 9px;
  flex: .2;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}
.list{
  width: 100%;
  margin-top: 18px;
}
.list ul{
  list-style: none;
}
.list ul li{
  display: flex;
  margin-bottom: 8px;
}
.list ul li span{
  border: 1px solid lightgrey;
  padding: 8px;
  flex:1;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
.list ul li button{
  padding: 8px;
  background: #000;
  color: #fff;
  border: none;
  cursor: pointer;
  flex:.2;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}

.welcome-note{
  font-size: 14px;
  color: lightgrey;
  text-align: center;
}
.emptyMessage{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 140px;
}
.emptyMessage img{
  width: 80px;
  opacity: .2;
}
